}
/* admin.css */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  max-width: 500px;
  width: 100%;
}

.modal-buttons {
  margin-top: 20px;
}

.modal-btn {
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  margin: 0 10px;
  cursor: pointer;
}

.modal-btn.yes {
background-color: inherit;
  color: black;
}
.modal-btn.yes:hover,.modal-btn.no:hover{
 background-color:#FFC107 ;
  border:2px solid black;
}

.modal-btn.no {
  background-color: inherit;
  color: black;
}


